<template>
	<view class="index">
		<canvas :canvas-id="canvasId" class="canvas" @touchstart="touch" @touchmove="move" @touchend="touchEnd" />
	</view>
</template>

<script>
	import uCharts from '@/js_sdk/u-charts/u-charts/u-charts.js';
	import httpUtils from '@/utils/httpUtils.js';
	import Tools from '@/utils/tools.js';
	import {
		bus
	} from '@/utils/bus.js';
	export default {
		props: {
			canvasId: {
				type: String,
				default: "canvasColumn"
			},
			legendShow: {
				type: Boolean,
				default: false
			},
			Unit: {
				type: String,
				default: ""
			},
			cWidth: {
				type: Number,
				default: uni.upx2px(680)
			},
			cHeight: {
				type: Number,
				default: uni.upx2px(500)
			},
			Column: {
				type: [Object, Array],
				default () {
					return {
						categories: [],
						series: []
					}
				}
			}
		},
		data() {
			return {
				canvaColumn: null
			}
		},
		created() {
			// bus.$on('changeEcharts',()=>{
			// 	this.showColumn(this.canvasId, this.Column);
			// })
			bus.$on('changeEcharts', (data) => {
				this.showColumn(this.canvasId, data);
			})
		},
		methods: {
			showColumn(canvasId, chartData) {
				for (let i = 0; i < chartData.series.length; i++) {
					chartData.series[i].format = (val) => {
						return val + this.Unit
					}
					chartData.series[i].color = '#03958C'
				}
				this.canvaColumn = new uCharts({
					$this: this,
					canvasId: canvasId,
					type: 'column',
					legend: {
						show: this.legendShow
					},
					fontSize: 11,
					animation: true,
					enableScroll: true,
					categories: chartData.categories,
					series: chartData.series,
					padding: [15, 10, 10, 20],
					xAxis: {
						itemCount: 7,
						disableGrid: true,
						rotateLabel: true
					},
					yAxis: {
						disabled: true,
						disableGrid: true,
						max: 7,
						format: (val) => {
							return val + this.Unit
						}
					},
					dataLabel: true,
					width: this.cWidth,
					height: this.cHeight,
					extra: {
						column: {
							width: 30
						}
					}
				});
			},
			toJSON() {},
			touch(e) {
				// this.canvaColumn.showToolTip(e, {
				// 	format: function(item, category) {
				// 		if (typeof item.data === 'object') {
				// 			return category + ' ' + ':' + item.data.value
				// 		} else {
				// 			return category + ' ' + ':' + item.data
				// 		}
				// 	}
				// });
			},
			move(e) {
				this.canvaColumn.scroll(e);
			},
			touchEnd(e) {
				this.canvaColumn.scrollEnd(e);
			},
		}
	}
</script>

<style lang="scss" scoped>
	.index {
		width: 100%;

		.canvas {
			width: 100%;
			height: 500upx;
		}
	}
</style>
